import { createSlice, configureStore } from "@reduxjs/toolkit";

const authSlice = createSlice({
  name: "Auth",
  initialState: {
    isLogin: false,
  },
  reducers: {
    login(state) {
      state.isLogin = true;
    },
    logout(state) {
      state.isLogin = false;
    },
  },
});

// actions
export const authActions = authSlice.actions;

// store we can pass only single reducer here
export const store = configureStore({
  reducer: authSlice.reducer,
});

// React redux => store, reducers , action ,slice
